/***
The base/ folder holds what we might call the boilerplate code for the project. 
In there, you might find the reset file, some typographic rules, and probably a 
stylesheet defining some standard styles for commonly used HTML elements.
***/
body {
  background-color: #f4f4f6;
  color: rgba(0, 0, 0, 0.695);
  font-weight: normal;
  font-style: normal;
  line-height: 1.68;
  letter-spacing: -0.003em;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden; }
  @media (min-width: 960px) {
    body {
      padding-left: 350px; } }

main {
  min-height: 75vh;
  padding-bottom: 118px;
  position: relative; }
  main > footer {
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0; }

/* Never like UIkit's red <em> color */
em {
  color: inherit;
  letter-spacing: -0.015em; }

/* Hides unnecessary content from home (index.html) page */
.home .container-overview {
  display: none; }

.label-radio,
.label-checkbox {
  cursor: pointer; }

/* stylelint-disable */
a, a:link, a:visited {
  text-decoration: none;
  word-break: break-word; }

a.token.url-link {
  color: #98c379;
  text-decoration: underline; }

a:hover,
a:link:hover,
a:visited:hover,
.uk-link:hover {
  text-decoration: none; }

.uk-container a {
  border-bottom: 1px solid #ddd; }

.uk-nav a {
  border-bottom: 0; }

.uk-container a:hover,
.uk-container a:active {
  border-bottom-color: var(--color-secondary); }

.uk-disabled {
  pointer-events: auto; }

a.uk-disabled:hover {
  color: #1e87f0;
  border-color: #ddd;
  cursor: not-allowed; }

/* TYPOGRAPHY */
main > article:first-of-type > h1 {
  border: none;
  padding-top: 0;
  margin-top: 0; }

main blockquote {
  border-left: 4px solid #ddd;
  color: #777;
  padding: 0 1rem; }

/* TYPOGRAPHY/WORD BREAKS */
.uk-article-title,
dd.tag-source,
table td {
  word-break: break-word; }

.description :not(pre) > code,
.description :not(pre) > kbd,
.description :not(pre) > samp {
  white-space: normal; }

.uk-text-accent {
  color: var(--color-accent) !important; }

@media screen and (min-width: 1200px) {
  /* Type breaks reset */
  .uk-article-title,
  dd.tag-source,
  table td {
    word-break: initial; }
  .description :not(pre) > code,
  .description :not(pre) > kbd,
  .description :not(pre) > samp {
    white-space: nowrap; } }

p:last-child, p:only-child {
  margin-bottom: 0 !important; }

.uk-text-small :not(pre) > code,
.uk-text-small :not(pre) > kbd,
.uk-text-small :not(pre) > samp {
  font-size: 0.75rem;
  padding: 2px 4px; }

