/***
For smaller components, there is the components/ folder. While layout/ is macro 
(defining the global wireframe), components/ is more focused on widgets. It 
contains all kind of specific modules like a slider, a loader, a widget, and 
basically anything along those lines. There are usually a lot of files in 
components/ since the whole site should be mostly composed of tiny modules.
***/
.jsdoc-item + .jsdoc-item {
  margin-top: 60px; }

.jsdoc-item .jsdoc-item-name {
  border: 0;
  color: #fff;
  background: var(--color-gradient);
  margin: 0;
  position: relative; }

.jsdoc-item.deprecated .jsdoc-item-name {
  background: var(--color-secondary); }

.jsdoc-item .jsdoc-item-name .type-signature {
  color: rgba(255, 255, 255, 0.75);
  font-style: italic;
  position: relative;
  bottom: 1px; }

.jsdoc-item .jsdoc-item-name .signature {
  color: #95ffd9; }

.jsdoc-item .jsdoc-item-name.uk-sticky.uk-active {
  border-top: 0;
  background: var(--color-secondary);
  width: 100%;
  padding: 0.75em 0 0.75em 3.85em;
  left: 350px; }

.jsdoc-item .jsdoc-item-name .uk-label {
  background: rgba(255, 255, 255, 0.125);
  clip-path: polygon(29% 0, 100% 0, 100% 100%, 51% 100%);
  line-height: 1;
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-end;
  justify-content: center;
  width: 120px;
  padding-bottom: 1px;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0; }

.jsdoc-item .jsdoc-item-params,
.jsdoc-item .jsdoc-item-props,
.jsdoc-item .js-docitem-props,
.jsdoc-item .js-docitem-params {
  border-width: 1px;
  border-style: solid;
  border-color: #e5e5e5;
  border-image: initial;
  padding: 0; }
  .jsdoc-item .jsdoc-item-params .subsection-title,
  .jsdoc-item .jsdoc-item-props .subsection-title,
  .jsdoc-item .js-docitem-props .subsection-title,
  .jsdoc-item .js-docitem-params .subsection-title {
