.jsdoc-item .jsdoc-item-details {
  font-size: 14px;
  line-height: 20px;
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-row-gap: 8px;
  grid-column-gap: 1rem;
  margin-top: 0; }
  @media (min-width: 1200px) {
    .jsdoc-item .jsdoc-item-details {
      grid-template-columns: repeat(4, auto); } }

.jsdoc-item .jsdoc-item-details > .container {
  display: flex;
  flex-direction: column; }

.jsdoc-item .jsdoc-item-details :not(pre) > code {
  color: initial;
  background: transparent;
  font-size: 90%;
  padding: 0 2px; }

.jsdoc-item .jsdoc-item-details ul {
  list-style-type: none;
  margin: 0; }

.jsdoc-item .jsdoc-item-details .param-desc .uk-text-meta {
  font-size: 12px; }

.jsdoc-item .jsdoc-item-details dd {
  margin-top: 0; }

.jsdoc-item .jsdoc-item-details dd:last-child {
  margin-bottom: 0; }

.jsdoc-item .jsdoc-item-details p {
  margin: 0; }

.jsdoc-item .jsdoc-item-details .param-desc p {
  display: inline-block; }

.jsdoc-item .jsdoc-item-details .prettyprint {
  margin: 0; }

.jsdoc-item .jsdoc-item-details .object-value {
  padding-top: 0; }

.jsdoc-item .jsdoc-item-details hr {
  margin: 0.5em 0; }

.jsdoc-item .uk-icon {
  border: 0;
  cursor: pointer;
  padding: 1.215rem;
  position: absolute;
  top: 0;
  left: -3.45rem;
  opacity: 0;
  visibility: hidden; }
  .jsdoc-item .uk-icon:hover, .jsdoc-item .uk-icon:focus {
    color: var(--color-primary);
    transform: scale(1.35); }

.jsdoc-item:hover .uk-icon, .jsdoc-item:focus .uk-icon {
  opacity: 1;
  visibility: visible;
  transition: 200ms ease-out; }

[data-type='member'] a::before {
  content: '#';
  display: inline-block;
  margin-left: -14px;
  margin-right: 5px; }

.intro header ~ .jsdoc-item-details {
