  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-row-gap: 8px;
  grid-column-gap: 1rem;
  margin-top: 0; }
  @media (min-width: 1200px) {
    .intro header ~ .jsdoc-item-details {
      grid-template-columns: repeat(4, auto); } }

.intro > .jsdoc-item .js-docitem-props {
  border-color: #bbb; }
  .intro > .jsdoc-item .js-docitem-props .subsection-title {
    color: #666; }

.description + .js-docitem-params {
  margin-top: 60px; }

.jsdoc-item-name .uk-badge {
  border-radius: 0;
  color: #fff;
  background: var(--color-danger);
  font-size: .75rem;
  letter-spacing: 0.025em;
  padding: 0 8px;
  margin: 0 0 1px 1rem;
  opacity: 0.875; }
  .jsdoc-item-name .uk-badge:hover, .jsdoc-item-name .uk-badge:focus {
    color: #fff;
    background: var(--color-danger); }

.jsdoc-item-todo .uk-label {
  border-radius: 0;
  font-size: .75rem;
  letter-spacing: 0.0075em;
