.jsdoc-item-tutorials {
  font-size: 14px;
  line-height: 20px; }

:not(pre) > code,
:not(pre) > kbd,
:not(pre) > samp {
  color: var(--color-secondary);
  background: var(--color-accent); }

pre {
  border: 0;
  border-radius: 0;
  color: #abb2bf;
  background-color: #282c34;
  padding: 0; }

div.code-toolbar > .toolbar {
