.treeview-part .entry-line {
  position: relative;
  text-indent: -99em;
  display: inline-block;
  vertical-align: top;
  width: 1.2em;
}
.treeview-part .entry-line:before,
.treeview-part .line-h:after {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
}
.treeview-part .line-h:before,
.treeview-part .line-v:before {
  border-left: 1px solid #555;
}
.treeview-part .line-v-last:before {
  height: 50%;
  border-left: 1px solid #555;
  border-bottom: 1px solid #555;
}
.treeview-part .line-h:after {
  height: 50%;
  border-bottom: 1px solid #555;
}
.treeview-part .entry-name {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 0 0 0 28px;
}
.entry-name:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 1em;
  padding: 0 4px;
  background: no-repeat 50% 50% / contain;
  color: #d4d7d6;
  content: '\E01D';
}
.dotfile {
  opacity: 0.5;
}
@font-face {
  src: url('../icons/seti.woff') format('woff');
  font-family: 'seti';
  font-weigth: normal;
  font-style: normal;
}
.entry-name:before {
  font: 150% 'seti';
}
.entry-name.ext-folder:before,
.entry-name.ext-folder-icon:before,
.entry-name.ext-rootfolder-icon:before {
  content: '\E020';
}
/* dirs */
.entry-name:first-of-type:before,
.entry-name.dir:before {
  content: '';
  background: no-repeat 50% 50% / contain;
  width: 1em;
  height: 0.75em;
  top: 2px;
  left: -1px;
}
/* root */
.entry-name:first-of-type:before,
.entry-name.dir:first-child:before {
  background-image: url('data:image/svg+xml;base64,');
}
/* open folders */
.entry-name.dir:nth-child(n+2):before {
  background-image: url('data:image/svg+xml;base64,');
}
